<template>
    <div class="appointment">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="more">
                <img class="more" src="../assets/img/more.png">
            </span>
        </div>
        <div class="lessonDetails">
            <div class="lessons">
              <div class="lesson">
                <div class="lessonLeft">
                  <span>{{ lessonInfo.fLessonDate }}</span>
                </div>
                <div class="lessonRight">
                  <ul class="lessonInfor">
                      <li>课程：{{ lessonInfo.fLessonName }}</li>
                      <li>价格：￥{{ lessonInfo.fLessonPrice}} 元</li>
                      <li>时间：{{ lessonInfo.fFargStart }}-{{ lessonInfo.fFragEnd }}</li>
                      <li>地址：{{ lessonInfo.fLessonAddress }}</li>
                  </ul>
                </div>
              </div>
              <div class="accountNum">
                  <span class="number">报名人数：</span>
                  <span class="textOrange">{{ lessonInfo.fOrderCnt }}/{{ lessonInfo.fLimitCnt }}</span>
              </div>
            </div>
        </div>
        <div class="paySection">
            <div class="payTitle">付款方式</div>
            <ul class="payMode">
                <li v-for="(payMode, index) in modes" :key="index" class="pay" @click="togglePayMode(index)">
                    <img class="selectImg" :src="payMode.isSelected  ? selected : unselected">
                    <img :src="payMode.icon" class="payIcon">
                    <span>{{ payMode.pay }}</span>
                </li>
            </ul>
        </div>
        <div class="footer">
            <span class="ensurePrice">
                <span>应付金额</span>
                <span>￥{{ lessonInfo.fLessonPrice }}</span>
            </span>
            <span class="ensure" @click="pay">立即支付</span>
        </div>
    </div>
</template>

<script>
import { Toast, MessageBox } from 'mint-ui'
import { getLessonInfo, appiont } from '../api/getdata'
import store from '../vuex/store'
export default {
  name: 'appointment',
  data () {
    return {
      lessonInfo: {},
      selected: require('../assets/img/selected.png'),
      unselected: require('../assets/img/unselected.png'),
      modes: [{
        pay: '余额支付', icon: require('../assets/img/yue.png'), isSelected: true
      }
      /*, {
        pay: '微信支付', icon: require('../assets/img/wechat.png'), isSelected: false
      }, {
        pay: '支付宝支付', icon: require('../assets/img/alipay.png'), isSelected: false
      }, {
        pay: '银行卡支付', icon: require('../assets/img/bankcard.png'), isSelected: false
      } */]
    }
  },
  mounted () {
    this.fLessonId = store.state.lessonId
    this.getLessonDetails()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    togglePayMode (index) {
      for (var i = 0; i < this.modes.length; i++) {
        this.modes[i].isSelected = false
        this.modes[index].isSelected = true
      }
    },
    getLessonDetails () {
      let data = getLessonInfo({lessonId: this.fLessonId})
      data.then(res => {
        console.log(res.data)
        this.lessonInfo = res.data
      })
    },
    pay () {
      MessageBox.confirm('', {
        message: '确认支付?'
      }).then(action => {
        if (action === 'confirm') {
          let data = appiont({lessonId: this.fLessonId})
          data.then(res => {
            if (res.status === '0') {
              Toast({
                message: '支付成功',
                duration: 1500
              })
              this.$router.push('/home/teachers/userInfor')
            } else {
              MessageBox.confirm('', {
                messsage: '余额不足,前往充值?'
              }).then(action => {
                if (action === 'confirm') {
                  this.$router.push('/home/mine/recharge')
                }
              })
            }
          })
        }
      }).catch(err => {
        if (err === 'cancle') {
          console.log('111')
          Toast({
            message: '已取消支付',
            duration: 1500
          })
        }
      })
    }
  }
}
</script>

<style scoped>
  * {
    font-size: 0.5rem;
    margin: 0;
    padding: 0;
  }

  .yearCard {
    width: 100%;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .more {
    height: 0.6rem;
    width: 0.13rem;
    position: relative;
  }

  .more img,
  .back img {
    width: 100%;
  }

.lessonDetails {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    background: #f5f5f5;
    border-radius: 0.2rem;
  }

  .lesson {
    display: flex;
    margin: 0.38rem auto;
    padding: 0.3rem 0.2rem;
    border-bottom: 1px solid #e6e6e6;
    color: #333333;
  }

  .lessonLeft span {
    width: 1.65rem;
    display: inline-block;
    vertical-align: top;
    font-size: 0.373333rem;
  }

  .lessonInfor {
    list-style-type: none;
    text-align: start;
  }

  .lessonInfor li {
    font-size: 0.373333rem;
  }

  .lessonRight {
    position: relative;
    margin-left: 0.48rem;
  }

  .accountNum {
    width: 100%;
    text-align: start;
    padding: 0 0 0.3rem 0.2rem;
  }

  .number {
    font-size: 0.4rem;
    color: #333333;
  }

  .textOrange {
    font-size: 0.4rem;
    color: #ff8309;
  }

  .paySection {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .payTitle {
    width: 100%;
    text-align: start;
    margin-top: 0.4rem;
  }

  .pay {
    height: 1.173333rem;
    padding: 0.2rem 0;
    border-bottom: 1px solid #e6e6e6;
    display: flex;
    align-items: center;
  }

  .payMode {
    margin-top: 0.1rem;
  }

  .payIcon {
    height: 100%;
    margin-left: 0.66667rem;
  }

  .payMode span {
    margin-left: 0.4rem;
    color: #333333;
  }

  .selectImg {
    width: 0.346667rem;
    height: 0.346667rem;
  }

  .footer {
    display: flex;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 1.3rem;
    border-top: 1px solid #e6e6e6;
  }

  .ensurePrice {
    width: calc(100% - 1.786667rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.373333rem;
  }

  .ensurePrice span:first-child {
    color: #333333;
    font-size: 0.4rem;
  }

  .ensurePrice span:last-child {
    color: #ff8309;
  }

  .ensure {
    width: 2.53333rem;
    background: #ffd200;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.4rem;
    color: #333333;
  }
</style>



